@charset "UTF-8";
* {
  box-sizing: border-box;
  color: #ffffff;
  font-family: "Montserrat";
}

body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #000000;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
}

img {
  max-width: 100%;
}

.section_title {
  margin: 20px 0 20px 70px;
  font-size: 30px;
  color: #ffffff;
}

.film-cards-gallery {
  position: relative;
  max-width: 1440px;
  padding: 40px 0;
  margin: 0 auto;
  background-color: #0a031c;
}

.gallery-wrapper {
  width: 90%;
  margin: 0px auto;
  padding: 20px 0;
  overflow-x: auto;
}

.gallery-wrapper::-webkit-scrollbar {
  display: none;
}

.movies-list, .series-list, .cartoon-list {
  display: flex;
  flex-wrap: nowrap;
  width: 1400px;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.movies-list__item {
  align-items: flex-start;
  margin: 0 20px;
  padding: 0;
  min-width: 200px;
  border-radius: 12px;
  transition: transform 0.4s;
}

.movies-list__item:hover {
  transform: scale(105%);
  box-shadow: 0px 0px 10px 10px rgba(255, 255, 255, 0.3);
}

.movies-list__link {
  display: inline-block;
}

.movies-list__img {
  height: 100%;
  border-radius: 12px;
  margin: auto 0;
  padding: 0;
}

.movies-gallery,
.series-gallery,
.cartoon-container {
  position: relative;
}

.gallery-btn--right, .gallery-btn--left {
  position: absolute;
  top: 250px;
  margin: 0;
  padding: 0;
  width: 37px;
  height: 48px;
  border-radius: 5px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.3);
  border: none;
}
.gallery-btn--right img, .gallery-btn--left img {
  width: 37px;
  height: 48px;
}

.gallery-btn--right {
  right: 80px;
}

.gallery-btn--left {
  left: 80px;
  transform: rotate(180deg);
}

.gallery-btn--right:hover {
  background-color: rgba(0, 0, 0, 0.9);
}

.gallery-btn--left:hover {
  background-color: rgba(0, 0, 0, 0.9);
}

.modal-window {
  display: none;
}

.modal-window--active {
  display: inline-block;
  position: absolute;
  left: 10px;
  top: 1%;
  width: 95%;
  margin: auto;
  z-index: 99;
  position: fixed;
}

.modal-window__container {
  position: relative;
  display: flex;
  justify-content: space-between;
  max-width: 1364px;
  margin: 0 auto;
  padding: 40px;
  border-radius: 20px;
  background-color: #000000;
}

.modal-window__container::after {
  content: "";
  top: -15px;
  left: -20px;
  position: absolute;
  height: 105%;
  width: 105%;
  background: linear-gradient(90deg, #FF4D6D, #54E1FF);
  z-index: -1;
  filter: blur(40px);
}

.modal-window__title {
  margin: 10px 0;
  font-size: 70px;
  font-weight: 900;
}

.modal-window__year,
.modal-window__country,
.modal-window__genres {
  margin: 15px 0 0 0;
  font-size: 22px;
}

.modal-window__img-box {
  min-width: 40%;
}
.modal-window__img-box img {
  width: 100%;
  border-radius: 20px;
}

.modal-window__description {
  width: 60%;
  margin: 20px 0 0 40px;
}

.modal-window__text {
  font-size: 20px;
  font-weight: 500;
  line-height: 30px;
}

.modal-window__btn {
  display: inline-block;
  position: absolute;
  top: 25px;
  right: 25px;
  width: 35px;
  height: 35px;
  border: none;
  border-radius: 5px;
  background-color: rgba(71, 71, 71, 0);
}
.modal-window__btn img {
  width: 30px;
  height: 30px;
}

@media screen and (max-width: 1043px) and (min-width: 768px) {
  .modal-window__title {
    font-size: 50px;
  }
  .modal-window__text {
    font-size: 16px;
    padding: 10px;
  }
  .modal-window__description {
    width: 100%;
    margin-left: 20px;
  }
  .modal-window__text {
    width: 100%;
  }
}
@media screen and (max-width: 767px) {
  .modal-window__container {
    flex-direction: column;
  }
  .modal-window__img-box {
    width: 100%;
  }
  .modal-window__description {
    margin: 0;
    padding: 0;
    width: 100%;
  }
  .modal-window__title {
    font-size: 50px;
  }
  .modal-window__text {
    width: 100%;
    font-size: 16px;
  }
}
.search-result {
  display: flex;
  flex-wrap: wrap;
  margin: 10px auto;
}

/* //slider styles// */
swiper-container {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 50px;
  margin-top: 10px;
}

swiper-slide {
  background-position: center;
  background-size: cover;
  width: 25%;
}

swiper-slide img {
  display: block;
  width: 100%;
  border-radius: 20px;
}

/* header styles */
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px;
  height: 60px;
  flex-wrap: wrap;
  margin-bottom: 10px;
}

.header__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px;
  height: 60px;
}

.header__left-part {
  display: flex;
}

.header__logo {
  font-size: 32px;
  text-decoration: none;
  color: aliceblue;
  width: 50px;
}

.header__search {
  padding: 8px;
  border: 2px solid #000000;
  border-radius: 5px;
  outline: none;
  color: #000000;
}

.header__search::-moz-placeholder {
  color: #999;
}

.header__search::placeholder {
  color: #999;
}

.header__list {
  font-size: 18px;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.footer__list {
  list-style-type: none;
  display: flex;
  gap: 15px;
  justify-content: center;
  flex-wrap: wrap;
}

.footer {
  margin-top: 10px;
  border-top: 1px solid aliceblue;
}

a {
  text-decoration: none;
}

a:hover {
  color: #FF4D6D;
}

.creators {
  text-align: center;
  font-size: 10px;
  color: #FF4D6D;
}

@media (max-width: 550px) {
  .header__list {
    font-size: 12px;
  }
  .footer__list {
    font-size: 12px;
  }
}
/* header end */
.popular-movies__container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 40px 0;
  position: relative;
}

.popular-movies {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
