/* RANDOM_FILM RART */
.random {
  position: relative;
  margin-bottom: 30px;
}

.random::after {
  content: "";
  top: 0%;
  left: 24%;
  position: absolute;
  height: 100%;
  width: 53%;
  background: linear-gradient(90deg, #FF4D6D, #54E1FF);
  z-index: -1;
  filter: blur(30px);
}

.random-container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  background: linear-gradient(90deg, rgb(10, 3, 28) 51%, rgb(52, 148, 168) 200%);
  background-color: #0a031c;
  margin: 50 auto;
  margin-bottom: 77px;
  max-width: 700px;
  height: 380px;
  -webkit-backdrop-filter: opacity(20%);
          backdrop-filter: opacity(20%);
  box-shadow: -1px -1px 61px 35px rgba(35, 49, 84, 0.64);
  -webkit-box-shadow: -1px -1px 61px 35px rgba(35, 49, 84, 0.64);
  -moz-box-shadow: -1px -1px 61px 35px rgba(35, 49, 84, 0.64);
